* {
  box-sizing: border-box;
  
}
 

body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}

.header {
  padding: 80px;
  text-align: center;
  background: rgb(34,193,195);
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,45,227,0.5522584033613445) 100%);
  color: black;
}
 

.header h1 {
  font-size: 40px;
  color: #FFFFFF;
  text-shadow: 5px 4px 2px rgba(128, 18, 64, 0.35),19px -1px 24px rgba(65, 12, 112, 1),-2px -3px 7px rgba(113, 64, 40, 1);
    
}
.navbar {
  overflow: hidden;
  background-color: #333;
}
 
.navbar a {
  float: left;
  display: block;
  color: white;
  text-align: center;
  padding: 14px 20px;
  text-decoration: none;
}
 
.navbar a.right {
  float: right;
}
 

.navbar a:hover {
  background-color: rgb(43, 216, 27);
  color: black;
}
 

.row {  
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-wrap: wrap; 
  flex-wrap: wrap;
}
 

.side {
  -ms-flex: 30%; 
  flex: 30%;
  background-color: #81d8c9;
  padding: 20px;
}
 

.main {   
  -ms-flex: 70%; 
  flex: 70%;
  background-color: rgb(34, 212, 212);
  padding: 20px;
}
 

.fakeimg {
  background-color: rgb(176, 182, 176);
  width: 100%;
  padding: 20px;
}
 
.footer {
  padding: 20px;
  text-align: center;
  background: rgb(34,193,195);
  background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(11,30,31,1) 15%, rgba(30,157,159,1) 20%,
                                    rgba(29,145,147,1) 27%, rgba(28,133,135,1) 34%, rgba(23,79,80,1) 65%, 
                                    rgba(22,63,64,1) 74%, rgba(19,29,30,1) 93%, rgba(19,24,25,1) 96%, 
                                    rgba(18,17,18,0.5522584033613445) 100%);
  color: white;
}
.maindiv
{
  margin:70px;
  width: 90%;
  text-align: center;
}
.fakeimg img.picc
{
  width: 100%;
height: 100%; 
border-radius: 30%;

-webkit-box-shadow: 9px 9px 9px 3px rgba(7,13,13,0.98); 
box-shadow: 9px 9px 9px 3px rgba(7,13,13,0.98);

}
.fakeimg img.piccc
{
